import handleActions from '../utils/handleActions'
import {
	ADD_BASKET_ITEM,
	AddBasketItemAction,
	REMOVE_BASKET_ITEM,
	RemoveBasketItemAction,
} from '../actions/basket'
import { IProduct } from './products'

export interface IBasketItem {
	product: IProduct
	quantity: number
}

export interface IBasketState {
	[id: string]: IBasketItem
}

const initialState: IBasketState = {} //{"ea6b1022-2953-4c19-84b5-237faf4d5158":{"product":{"id":"ea6b1022-2953-4c19-84b5-237faf4d5158","name":"Ananasáč","price":50,"volume":0.25,"image":"https://s3.eu-central-1.amazonaws.com/goodlok.mgw.cz/icons/1521815960-2_ananas-malej.png","color":"#E66172","ingredients":["Pomeranč","Jablko","Citron","Ananas"]},"quantity":2},"63cf9c1f-60b1-487f-af07-ab2bda1c94c4":{"product":{"id":"63cf9c1f-60b1-487f-af07-ab2bda1c94c4","name":"Ananasovo-kokosový superfood","price":60,"volume":0.25,"image":"https://s3.eu-central-1.amazonaws.com/goodlok.mgw.cz/icons/1521813331-2_769.png","color":"#F69D00","ingredients":["Jablko","Banán","Špenát","Citron","Ananas","Kokos"]},"quantity":2},"a08e3d43-0bac-4261-a056-aa46ae46cc66":{"product":{"id":"a08e3d43-0bac-4261-a056-aa46ae46cc66","name":"100% Mrkev","price":90,"volume":0.5,"image":"https://s3.eu-central-1.amazonaws.com/goodlok.mgw.cz/icons/1521649311-2_mrkev.png","color":"#99D25D","ingredients":["Mrkev"]},"quantity":3}}

// tslint:disable:function-name
export default handleActions(
	{
		[ADD_BASKET_ITEM](state, { payload }: AddBasketItemAction) {
			const newState = {
				...state,
			}

			if (newState[payload.id]) {
				newState[payload.id].quantity += 1
			} else {
				newState[payload.id] = {
					product: payload,
					quantity: 1,
				}
			}
			return newState
		},
		[REMOVE_BASKET_ITEM](state, { payload }: RemoveBasketItemAction) {
			const newState = {
				...state,
			}

			if (newState[payload.id]) {
				newState[payload.id].quantity -= 1

				if (newState[payload.id].quantity === 0) {
					delete newState[payload.id]
				}
			}
			return newState
		},
	},
	initialState,
)



